.todoItem{display:flex;cursor:pointer;line-height:2.5;border-bottom:1px solid #f7f7f7;font-size:16px;padding:0 10px}.todoItem:hover{background:#f7f7f7}.todoItem:hover .remove{opacity:1}.todoItem:last-child{border-bottom:0px}.todoText{flex:1}.remove{color:salmon;opacity:0;padding:0 10px 0 0;transition:all ease 0.3s}.check{color:#ccc}.checked{text-decoration:line-through;color:#ccc}.checked+.check{color:#377dff}
